.conteudoCardHorariosDisponiveis{
    background-color: #F4F3EE;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid #082031;
    border-radius: 5px;
    width: 180px;
    height: 50px;
    cursor: pointer;
}

.horario{
    color: #082031;
}

.conteudoCardHorariosDisponiveis:hover{
    background-color: #d8b684;
}

.selecionado {
    background-color: #e3a74f;
}